* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.AppDiv {
  display: grid;
  grid-row-gap: 4em;
  row-gap: 4em;
}
.Header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 2rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Logo-Image {
    display: flex;
    align-items: center;
    grid-column-gap: 0.5rem;
    -webkit-column-gap: 0.5rem;
            column-gap: 0.5rem;
    font-size: 22px;
    font-weight: bold;
    color: #8f8ee0;
}

.Logo-Image>img {
    width: 4.5rem;
}

.HomeTab {
    font-size: 22px;
    font-weight: bold;
    color: #8f8ee0;
    border: 3px solid #6d67ef;
    border-radius: 10px;
    padding: 0.3rem 0.8rem 0.4rem 0.8rem;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);
    text-decoration: none;
}

.HomeTab:hover {
    background-color: #f7f7fd;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 5px 15px 0 rgba(0, 0, 0, 0.19);
    position: relative;
    bottom: 1px;
}

.Search {
    border: 4px solid lightgray;
    padding: 0.2rem 0.8rem;
    border-radius: 1.5rem;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);
}

.Search>label {
    display: flex;
    align-items: center;
}

.Search>label>input {
    outline: none;
    border: none;
    width: 15rem;
    padding: 0.5rem 0 0.5rem 0.8rem;
}

.cartIcon {
    position: relative;
    border: 2.5px solid #6d67ef;
    border-radius: 2rem;
    padding: 0.5rem 0.6rem;
    background-color: #fbfbfd;
    color: #574fdf;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 3px 8px 0 rgba(0, 0, 0, 0.19);
}

.cartIcon:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 5px 15px 0 rgba(0, 0, 0, 0.19);
    position: relative;
    bottom: 1px;
    background-color: #f3f3f8;
}

.cartCount {
    position: absolute;
    bottom: 1.5rem;
    color: white;
    padding: 0.2rem 0.5rem;
    font-weight: bold;
    font-size: 18px;
    background-color: rgb(228, 114, 114);
    border-radius: 1rem;
}

.Footer {
    padding: 2rem 0;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: #7574e0;
    box-shadow: 0 -3px 15px -8px #333;
}

@media screen and (min-width: 1250px) {
    .Header {
        padding: 0.1rem 3rem 0.1rem 0.5rem;
    }

    .Logo-Image {
        grid-column-gap: 0.5rem;
        -webkit-column-gap: 0.5rem;
                column-gap: 0.5rem;
    }

    .Search>label>input {
        width: 20rem;
    }
}

@media screen and (max-width:550px) {
    .Search {
        display: none;
    }

    .Header {
        padding-right: 1.5rem;
    }
}
.ProductMain {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 2.5rem;
    -webkit-column-gap: 2.5rem;
            column-gap: 2.5rem;
    grid-row-gap: 2.5rem;
    row-gap: 2.5rem;
    padding: 0 2rem;
    min-height: 85vh;
}

.Main-2 {
    padding: 0 2rem;
    min-height: 85vh;
}

.Products {
    padding: 0.5rem 1rem 1rem 1rem;
    border: 2.5px solid #bab9ff;
    border-radius: 18px;
    box-shadow: 0 5px 10px 0 rgba(105, 100, 100, 0.2), 0 10px 14px 0 rgba(117, 109, 109, 0.19);
}

.Products:hover {
    position: relative;
    box-shadow: 0 15px 20px 0 rgba(105, 100, 100, 0.2), 0 18px 25px 0 rgba(117, 109, 109, 0.19);
    bottom: 0.1rem;
    border: 2.8px solid #bab9ff;
}

.Carts {
    display: flex;
    grid-column-gap: 0.5rem;
    -webkit-column-gap: 0.5rem;
            column-gap: 0.5rem;
    padding: 0.5rem 2rem;
    border: 3px solid #bab9ff;
    border-radius: 15px;
    box-shadow: 0 3px 8px 0 rgba(105, 100, 100, 0.2), 0 8px 12px 0 rgba(117, 109, 109, 0.19);
}

.C-Remove {
    color: #f54242;
}

.C-Remove,
.bi-dash-circle,
.bi-plus-circle:hover {
    cursor: pointer;
}

.P-Image {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem 0 1.5rem 0;
}

.C-Image>img {
    width: 100%;
    height: 10.3vh;
    object-fit: fill;
}

.P-Image>img {
    width: 100%;
    height: 40vh;
    object-fit: fill;
}

.P-DetailDiv {
    border-top: 2px solid #bab9ff;
    display: grid;
    grid-row-gap: 0.6rem;
    row-gap: 0.6rem;
    padding: 1.5rem 0 1.7rem 0;
}

.P-Title,
.C-Title {
    font-size: 20px;
    font-weight: bold;
    color: #4c4cbb;
}

.C-Title {
    display: grid;
}

.P-Price,
.C-Price,
.C-Dis {
    font-size: 18px;
    font-weight: bold;
    color: rgb(52, 163, 107);
}

.C-Title,
.C-Price,
.C-Dis {
    font-size: 24px !important;
}

.C-Quantity {
    display: flex;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    color: #225ac2;
    font-size: 24px;
    font-weight: bold;
}

.Description {
    color: #8a88e4;
}

.Products>button {
    background-color: #6363cf;
    color: #e7e7f7;
    font-weight: 500;
    font-size: 18px;
    border: 3px solid #c4c4ee;
    border-radius: 10px;
    padding: 0.5rem 0.8rem;
}

.Products>button:hover {
    background-color: #6f6fe7;
    cursor: pointer;
}

.CartMain {
    display: grid;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    border: 5px solid #bab9ff;
    border-radius: 20px;
    padding: 1rem;
    box-shadow: 0 5px 10px 0 rgba(105, 100, 100, 0.2), 0 10px 14px 0 rgba(117, 109, 109, 0.19);
}

.CartMain>h1 {
    color: #7272d8;
    background-color: #e7e7f7;
    border: 3px solid #bab9ff;
    border-radius: 20px;
    text-align: center;
    padding: 0.3rem 0;
}

.Empty {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    padding: 1.5rem;
    color: #6969e6;
}

.Total {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-row-gap: 1.8rem;
    row-gap: 1.8rem;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    border-top: 4px solid #bab9ff;
    padding: 2rem 0 1rem 0;
    font-size: 25px;
    font-weight: bold;
}

.Total>div {
    border: 3.5px solid #bab9ff;
    border-radius: 25px;
    padding: 0.5rem 0.8rem;
}

.CouponInput {
    display: flex;
    /* column-gap: 2%; */
    padding: 0 !important;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    border: none !important;
}

.CouponInput>input {
    padding: 0.5rem 1rem;
    width: 70%;
    border-radius: 22px 0 0 22px;
    border: 2px solid #bab9ff;
    outline: none;
    color: #7272d8;
    font-size: 20px;
    font-weight: bold;
}

.CouponInput>input:focus {
    outline: 4.5px solid rgba(105, 105, 230, 0.18);
    transition: 0.12s ease-in-out;
}

.CouponInput>button {
    width: 30%;
    border-radius: 0 22px 22px 0;
    border: 2px solid #bab9ff;
    border-left: none;
    background-color: #e7e7f7;
    color: #7272d8;
    font-size: 20px;
    font-weight: bold;
}

.CouponInput>button:hover {
    background-color: #7272d8;
    color: #e7e7f7;
}

@media screen and (min-width:551px) and (max-width:950px) {
    .ProductMain {
        grid-template-columns: repeat(2, 1fr);
        padding: 0 4rem;
    }

    .Carts {
        justify-content: space-between;
        align-items: center;
    }

    .C-Image {
        width: 10%;
    }

    .C-Image>img {

        height: 6vh;
    }

    .C-Title {
        width: 52%;
    }

    .C-Price {
        width: 18%;
    }

}

@media screen and (max-width:550px) {
    .ProductMain {
        grid-template-columns: repeat(1, 1fr);
        padding: 0 3rem;
    }

    .C-Image>img {
        width: 90%;
    }

    .Carts {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-row-gap: 1.5rem;
        row-gap: 1.5rem;
        padding: 1rem 1rem;
        grid-column-gap: 1rem;
        -webkit-column-gap: 1rem;
                column-gap: 1rem;
    }

}

@media screen and (min-width:951px) {
    .Carts {
        justify-content: space-between;
        align-items: center;
    }

    .C-Image {
        width: 6%;
    }

    .C-Title {
        width: 40%;
    }

    .C-Price {
        width: 14%;
    }

    .Total {
        grid-template-columns: repeat(2, 1fr);
    }
}

.Hide {
    display: none;
}

.Show {
    display: block;
    font-size: 16px !important;
    color: #f54242;
}

.CouponWrong {
    display: flex;
    /* column-gap: 2%; */
    padding: 0 !important;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    border: none !important;
}

.CouponWrong>input {
    padding: 0.5rem 1rem;
    width: 70%;
    border-radius: 22px 0 0 22px;
    border: 2px solid #e07b8c;
    outline: none;
    color: #e95971;
    font-size: 20px;
    font-weight: bold;
}

.CouponWrong>input:focus {
    outline: 4.5px solid rgba(235, 104, 121, 0.2);
    transition: 0.12s ease-in-out;
}

.CouponWrong>button {
    width: 30%;
    border-radius: 0 22px 22px 0;
    border: 2px solid #bab9ff;
    border-left: none;
    background-color: #e7e7f7;
    color: #7272d8;
    font-size: 20px;
    font-weight: bold;
}

.CouponWrong>button:hover {
    background-color: #7272d8;
    color: #e7e7f7;
}
